const currentPosition = new THREE.Vector2();

const force = new THREE.Vector2();
const velocity = new THREE.Vector2();
const tmpVec2 = new THREE.Vector2();
let mousePosition = new THREE.Vector2();
const mass = 2;

//Tooltip
