mainSceneCamera.position.z = 12.5;
Mainscene.background = new THREE.Color('#eb4922');
Mainscene.add(mainSceneCamera);


loadFont('fonts/AppleTea.fnt', (err, font) => {
	// Create a geometry of packed bitmap glyphs
	const geometry = createGeometry({
		font,
		text: 'Kaleidoscope'
	});

	// Load texture containing font glyphs
	const loader = new THREE.TextureLoader();
	loader.load('fonts/AppleTea.png', (texture) => {
		// Start and animate renderer
		init(geometry, texture);
	});
});

let textMesh;
function init(geometry, texture) {
	// Create material with msdf shader from three-bmfont-text
	const material = new THREE.RawShaderMaterial(MSDFShader({
		vertexShader,
		map: texture,
		side: THREE.DoubleSide,
		transparent: true,
		negate: false,
	}));

	// Create time uniform from default uniforms object
	material.uniforms.time = new THREE.Uniform('f', 0);

	// Create mesh of text       
	textMesh = new THREE.Mesh(geometry, material);
	textMesh.position.set(-165, 190, -350); // Move according to text size
	textMesh.rotation.set(Math.PI, 0, 0); // Spin to face correctly
	Mainscene.add(textMesh);
}


