//Loadding models
const gltfLoader = new GLTFLoader();
let mixer = null;
let kaleidoscopeScene = null;
let turningAnimation = null;
let scopeMesh = null;
const meshes = new Array();
let startQuaternion = new THREE.Quaternion();
gltfLoader.load(
	'/models/scene.glb',
	(gltf) => {
		kaleidoscopeScene = gltf.scene;

		//animations
		mixer = new THREE.AnimationMixer(kaleidoscopeScene);
		turningAnimation = mixer.clipAction(gltf.animations[0]);
		turningAnimation.setLoop(THREE.LoopOnce)
		turningAnimation.clampWhenFinished = true

		Mainscene.add(kaleidoscopeScene);
		kaleidoscopeScene.rotation.y = 4.7;
		startQuaternion.copy(kaleidoscopeScene.quaternion);

		gltf.scene.traverse(node => {
			if (node.name == 'Circle_primitive0') {
				scopeMesh = node;
				meshes.push(node);
			}
			else if (node.name == 'Circle_primitive1') {
				node.material = scopePlaneMat;
				meshes.push(node);
			}
			else if (node.name == 'Circle_primitive2') {
				meshes.push(node);
			}
		});

	}
)

