//Creating the shape that is reflected as texture
let geometry;
let material;
let obj;
var allObjects = new Array();
var objectRotations = new Array();
var numObjects =10;
var minRot = -0.05;
var maxRot = 0.05;

var colors = ['#272324', '#fffff6', '#0c77a8', '#ffa330', '#ffc5b4', '#e0ff6e', '#eb4922', '#ff8142', '#0ca8a3','#faff6e']
//var colors = ['#272324', '#fffff6', '#00afff', '#ff8e00', '#ffc5b4', '#d8ff4a', '#ff3100']

function createBufferShape() {
	for (var i = 0; i < numObjects; i++) {

		geometry = new THREE.TorusKnotGeometry(1, 1,10,20,19,7 );
		material = new THREE.MeshBasicMaterial({ color: colors[i], transparent: true, opacity:0.65});
		obj = new THREE.Mesh(geometry, material);
		Bufferscene.add(obj);
		allObjects.push(obj);

		obj.rotation.y = Math.PI * (1 / i * 6);
		obj.position.y = obj.position.y + (Math.random() * (2 + 2) - 2);
		obj.position.x = obj.position.x + (Math.random() * (1 + 1) - 1);
		obj.position.z = obj.position.z + (Math.random() * (1 + 1) - 1);

		var rotationVals = { x: Math.random() * (maxRot - minRot) + minRot, y: Math.random() * (maxRot - minRot) + minRot, z: Math.random() * (maxRot - minRot) + minRot };
		objectRotations.push(rotationVals);
	}
}

createBufferShape();


