//Kaleidoscope pattern shader
var numAxes = 12;
var tileHolder;

var patternState =  -1;
var numPatternState = 3;

tileMat.onBeforeCompile = (shader) => {

	shader.uniforms.time = { value: 0 }
	shader.uniforms.mousePosition = new THREE.Uniform(new THREE.Vector2())
    shader.uniforms.patternState = { value: patternState }

	shader.vertexShader = `
        uniform float time;
		uniform vec2 mousePosition;
	    uniform int patternState;
    ` + shader.vertexShader

	shader.fragmentShader = `
		uniform vec2 mousePosition;
		uniform int patternState;
    ` + shader.fragmentShader

	const token = '#include <begin_vertex>'
	const customTransform = `
		vec4 myworldPosition = modelMatrix * vec4(position.xyz, 1.0);
        vec3 transformed = vec3(position);
		
		vec2 dv =  myworldPosition.xy;
		float freq =length(dv);
		dv = normalize(dv);

		float amp = 0.15;
		float angle = -time * 10.0 + freq *6.0;
            
        if(patternState==1){
             transformed.x += sin(angle)*(amp);
        }
        else if(patternState==2){
             transformed.x += cos(angle)*(amp*2.0);
             transformed.z += sin(angle)*(amp*1.5);
             transformed.y += sin(angle)*(amp*1.5);
        } else {
			transformed.y += sin(angle)*amp;
		}
    `

	const fragmentToken = '#include <map_fragment>'
	const customColor = `
			#ifdef USE_MAP
			vec2 uv = vUv; 
            if(patternState>0){
			    float mouseCord=-0.7 + (mousePosition.y - -1.0) * (0.7 - -0.7) / (1.0 - -1.0); 
			    uv = vUv +vec2(mouseCord, mouseCord)*0.8;
            }
		    vec4 texelColor = texture2D( map, uv );
		    texelColor = mapTexelToLinear( texelColor );
		    diffuseColor *= texelColor;
			#endif
    `

	shader.vertexShader = shader.vertexShader.replace(token, customTransform)
	shader.fragmentShader = shader.fragmentShader.replace(fragmentToken, customColor)

	shader.needsUpdate = true;
	tileMat.userData.shader= shader
}


//Creating the pattern reflections
function updateGridGeometry() {
	scopeScene.remove(tileHolder);
	tileHolder = new THREE.Object3D();

	var theta = 0;
	var numSteps = numAxes;
	var step = 2 * Math.PI / numSteps;
	var radius = 1;

	const tileGeometry = new THREE.BufferGeometry();

	const vertices = new Array();
	const indices = new Array();

	vertices.push(0,0,0);

	var snapStep; // number of steps between simplified shape vertices
	var stepAngle;
	var rotOffset;

	// find out if numAxes is factor of 4 or 6
	if (!(numAxes % 6)) {
		// factor of 6
		snapStep = numAxes / 6;
		stepAngle = (2 * Math.PI) / 6;
		rotOffset = stepAngle / 2;

	}
	else {
		// factor of 4
		snapStep = numAxes / 4;
		stepAngle = (2 * Math.PI) / 4;
		rotOffset = 0;
	}

	// compute tile width
	var p1 = new THREE.Vector2(radius * Math.cos(0), radius * Math.sin(0));
	var p2 = new THREE.Vector2(radius * Math.cos(stepAngle), radius * Math.sin(stepAngle));
	var dist = p1.distanceTo(p2);
	var a = dist / 2;
	var c = radius;
	var b = Math.sqrt(c * c - a * a);
	var tileWidth;
	var tileHeight;
	var tileRowOffset;

	// find out if numAxes is factor of 4 or 6
	if (!(numAxes % 6)) {
	
		tileWidth = b * 2;
		tileHeight = c + a;
		tileRowOffset = b;
	}
	else {

		tileWidth = c * 2;
		tileHeight = c;
		tileRowOffset = c;
	}

	// add vertices
	for (var i = 0; i < numSteps; i++) {
		var mod = i % snapStep;
		var ratio = mod / snapStep;
		var position = Math.floor(i / snapStep);
		var angle1 = stepAngle * position;
		var angle2 = stepAngle * (position + 1);
		var x, y;

		if (mod == 0) {
			// standard vertex position
			x = radius * Math.cos(theta);
			y = radius * Math.sin(theta);
		}
		else {
			// interpolate between angle1 and angle2
			var x1 = radius * Math.cos(angle1);
			var y1 = radius * Math.sin(angle1);

			var x2 = radius * Math.cos(angle2);
			var y2 = radius * Math.sin(angle2);

			x = x1 + ((x2 - x1) * ratio);
			y = y1 + ((y2 - y1) * ratio);
		}

		vertices.push(x, y, 0);
		theta += step;
	}

	// add faces
	for (var i = 0; i < numSteps; i++) {
		var v1 = i + 1;
		var v2 = i + 2;
		if (v2 > numSteps) v2 = 1;
		indices.push(0, v1, v2);
	}

	const UVs = new Array();
	var mapWidth = 1 / snapStep;
	var diff = 1 - mapWidth;
	var mapLeft = diff / 2;
	var mapRight = 1 - diff / 2;

	UVs.push(0.5,0);
	for (var i = 0; i < numSteps; i++) {
		if (i % 2) {
			UVs.push(1, 1);
		}
		else {
			UVs.push(0,1);
        }
    }
	tileGeometry.setIndex(indices);
	tileGeometry.setAttribute('position', new THREE.BufferAttribute(new Float32Array(vertices), 3));
	tileGeometry.setAttribute('uv', new THREE.BufferAttribute(new Float32Array(UVs), 2));

	tileGeometry.computeBoundingSphere();
	tileGeometry.computeBoundingBox();

	var tileRow = new THREE.Object3D();
	tileHolder.add(tileRow);

	var scale = bufferSize / 3;
	var tileMesh = new THREE.Mesh(tileGeometry, tileMat);
	tileMesh.scale.set(scale, scale, 1);
	tileMesh.rotation.z = rotOffset;
	tileRow.add(tileMesh);

	var tileCountX = 15;
	for (var i = 0; i < tileCountX; i++) {
		var tileMeshLeft = tileMesh.clone();
		tileMeshLeft.position.x -= (tileWidth * scale) * (i + 1);
		tileRow.add(tileMeshLeft);

		var tileMeshRight = tileMesh.clone();
		tileMeshRight.position.x += (tileWidth * scale) * (i + 1);
		tileRow.add(tileMeshRight);
	}

	var tileCountY = 10;
	for (var i = 0; i < tileCountY; i++) {
		var tileRowTop = tileRow.clone();
		tileRowTop.position.y += tileHeight * scale * (i + 1);
		if (!(i % 2)) tileRowTop.position.x += tileRowOffset * scale;
		tileHolder.add(tileRowTop);

		var tileRowBottom = tileRow.clone();
		tileRowBottom.position.y -= tileHeight * scale * (i + 1);
		if (!(i % 2)) tileRowBottom.position.x += tileRowOffset * scale;
		tileHolder.add(tileRowBottom);
	}

	scopeScene.add(tileHolder);

}
updateGridGeometry();



//shader for the texture seen through scope
scopePlaneMat.onBeforeCompile = (shader) => {
	shader.fragmentShader = `
    ` + shader.fragmentShader

	const fragmentToken = '#include <map_fragment>'
	const customColor = `
			#ifdef USE_MAP
				vec2 texelSize = 1.0 / vec2(textureSize(map, 0));
				vec2 uv = gl_FragCoord.xy * texelSize;
				vec4 texelColor = texture2D( map, uv );
				texelColor = mapTexelToLinear( texelColor );
				diffuseColor *= texelColor;
			#endif
    `
	shader.fragmentShader = shader.fragmentShader.replace(fragmentToken, customColor)
	shader.needsUpdate = true;
	scopePlaneMat.userData.shader = shader
}


//Renderer
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})

renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
renderer.setSize(sizes.width, sizes.height);

renderer.setRenderTarget(bufferTexture);
renderer.render(Bufferscene, camera);

renderer.setRenderTarget(scopeTexture);
renderer.render(scopeScene, sceneCamera);

renderer.setRenderTarget(null);
renderer.render(Mainscene, mainSceneCamera);

updateCameraPostion(new THREE.Vector2(0.5, 0.5));

var startFrame = true;
let previousTime = 0;
let currentIntersect = null;
var animationTime = 0.0;
var animationDuration = 1.4;
var uiDuration = 0.69;
var uiVisible = true;

function updateCameraPostion(position) {
	camera.position.x = Math.sin(position.x * Math.PI * 2) *1;
	camera.position.z = Math.cos(position.x * Math.PI * 2) * 1;
	camera.position.y = position.y * 3
}
const tick = () => {
	//get time
	const elapsedTime = clock.getElapsedTime()
	const deltaTime = elapsedTime - previousTime
	previousTime = elapsedTime

	//rotate all objects
	for (var i = 0; i < allObjects.length; i++) {
		allObjects[i].rotation.z = elapsedTime * Math.PI * objectRotations[i].z;
		allObjects[i].rotation.y = elapsedTime * Math.PI * objectRotations[i].y;
		allObjects[i].rotation.x =elapsedTime * Math.PI * objectRotations[i].x;
	}

	switch (currentState) {
		case stateEnum.Intro:
			if (kaleidoscopeScene != null) {
				kaleidoscopeScene.rotation.y += 0.02;
			}
			//raycasting to objects from mouse
			if (scopeMesh != null) {
				raycaster.setFromCamera(targetPosition, mainSceneCamera)
				const intersects = raycaster.intersectObjects(meshes)

				if (intersects.length > 0) {
					if (currentIntersect == null) {
						scopeMesh.material.color.set('#ffd299')
						currentIntersect = intersects[0];
					}
				}
				else {
					if (currentIntersect != null) {
						scopeMesh.material.color.set('#ffffff')
						currentIntersect = null;
					}
				}
			}	
			break;

		case stateEnum.Animation:
			animationTime += deltaTime;
			if (mixer != null) {
				mixer.update(deltaTime);
			}
			if (animationTime >= animationDuration) {
				setScopeState();
			}
			if (uiVisible && animationTime >= uiDuration) {
				hideUI();
				uiVisible = false;
            }

			break;

		case stateEnum.TurningToAnimate:
			if (kaleidoscopeScene.quaternion.equals(startQuaternion)) {
				playAnimation();
			}
			else{
				kaleidoscopeScene.quaternion.rotateTowards(startQuaternion, 2.7 * deltaTime);
			}

			break;

        case stateEnum.Scope:

			let w = 0.1;
			force.subVectors(currentPosition, targetPosition); 
			force.multiplyScalar(-w * w);
			tmpVec2.copy(velocity);
			tmpVec2.multiplyScalar(2 * w);
			force.sub(tmpVec2);
			velocity.addScaledVector(force, 1 / mass);
			currentPosition.add(velocity);

			if (patternState > -1 ) {
				updateCameraPostion(currentPosition);
			}
			mousePosition = currentPosition.multiplyScalar(0.5);
			tileMat.userData.shader.uniforms.mousePosition.value = mousePosition;
	
			break;
		default:
			break;
    }

	//movement of the shader
	tileMat.userData.shader.uniforms.time.value = elapsedTime * 0.3;

	if (textMesh) {
		textMesh.material.uniforms.time.value = elapsedTime;
		textMesh.material.uniformsNeedUpdate = true;

	}

    // Render
    renderer.setRenderTarget(bufferTexture);
    renderer.render(Bufferscene, camera);

	renderer.setRenderTarget(scopeTexture);
	renderer.render(scopeScene, sceneCamera);

	renderer.setRenderTarget(null);
	renderer.render(Mainscene, mainSceneCamera);

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
	if (startFrame) {
		startFrame = false;
	}
}

tick();



const hideUI = () => {
}

const playAnimation = () => {
	currentState = stateEnum.Animation;
	turningAnimation.play();
	animationTime = 0;
}


const setScopeState = () => {
	toolTip.style.visibility = 'visible';
	currentState = stateEnum.Scope;
}


//Full screen handling
window.addEventListener('dblclick', () => {
    const fullscreenElement = document.fullscreenElement || document.webkitFullscreenElement

    if (!fullscreenElement) {
        if (canvas.requestFullscreen) {
			canvas.requestFullscreen();
        }
        else if (canvas.webkitRequestFullscreen) {
			canvas.webkitRequestFullscreen();
        }
    }
    else {
        if (document.exitFullscreen) {
			document.exitFullscreen();
        }
        else if (document.webkitExitFullscreen) {
			document.webkitExitFullscreen();
        }
    }
})


var toolTipVisible = true;
//clicking
window.addEventListener('click', () => {
	if (currentState == stateEnum.Intro) {
		if (currentIntersect) {
			toolTip2.style.visibility = 'hidden';
			currentState = stateEnum.TurningToAnimate;
		}
	}
	else if (currentState == stateEnum.Scope) {
		if (toolTipVisible==true) {
			toolTip.style.visibility= 'hidden';
			toolTipVisible = false;
        }
        patternState = (patternState + 1) % numPatternState;
        tileMat.userData.shader.uniforms.patternState.value = patternState;

        //if (patternState==0) {
        //    toolTip.style.visibility = 'visible';
        //    toolTipVisible = true;
        //}
	}
})

//resizing
window.addEventListener('resize', () => {
    sizes.width = window.innerWidth;
    sizes.height = window.innerHeight;

    //update the camera properties
	mainSceneCamera.aspect = sizes.width / sizes.height;
	mainSceneCamera.updateProjectionMatrix();

    //Updating renderer
    renderer.setSize(sizes.width, sizes.height);
})

